body, *{padding: 0;margin: 0;}

body{    
    background-repeat: repeat;
    background-color: #372811;
    font-family: Arial, Helvetica, sans-serif;
}

#container{
    width: 100%;
    text-align: center;
    background-color: rgba(0, 10, 0,0.9);
    padding-bottom: 31px;
}

#startimg{
    max-width: 100%;
}
#hnyimg{max-width: 90%;}

#yearimg{
    max-width: 90%;
}

#msg{
    color: #fffffe;
    width: 70%;
    margin: 20px auto;
    line-height: 2;
    font-weight: bold;
    text-shadow: 1px 1px 1px red, 0px 0px 9px #00ff0a;
    font-size: 20px;
}
#username{
    color: #fff;
    margin: 50px 5px;
}

h2{color: #ffc56f;text-shadow: 1px 1px 1px #000, 0px 0px 22px #009688, 2px -1px 1px #ff0202;}